  const getAmounts = async () => {
    const amounts = []
    for (let i = 0; i < accounts.length; i++) {
      if (accounts[i] !== null) {
        let account = web3.eth.accounts.privateKeyToAccount(accounts[i])
        let amount = await web3.eth.getBalance(account.address)
        amounts.push(amount)
      }
    }
    setAmounts(amounts)
  }

